import React from 'react'

export default function Personaldetails(props) {
    return (
        <div className="bb text-center">
            <div className="form-signin bg-light">
                <form onSubmit={props.nextStep}>
                    <img className="mb-4" src="https://www.dropbox.com/s/zgbbayj1iqd9fjf/CF_Mark.jpg?raw=1" alt="" width="72"/>
                    <h1 className="h3 mb-3 fw-normal">Please Fill in The information below</h1>
        
                    <div className="form-floating">
                        <input required value={props.values.firstName} onChange={props.change('firstName')} type="text" className="form-control" id="floatingfName" />
                        <label htmlFor="floatingfName">First name</label>
                    </div>
                    <div className="form-floating">
                        <input required value={props.values.lastName} onChange={props.change('lastName')} type="text" className="form-control" id="floatinglName" />
                        <label htmlFor="floatinglName">Last name</label>
                    </div>
                    <div className="form-floating">
                        <input required value={props.values.email} onChange={props.change('email')} type="email" className="form-control" id="floatingInput"/>
                        <label htmlFor="floatingInput">Email address</label>
                    </div>
                    <div className="form-floating">
                        <input required value={props.values.country} onChange={props.change('country')} type="text" className="form-control" id="country" />
                        <label htmlFor="country">Country</label>
                    </div>
                    <div className="form-floating">
                        <input required value={props.values.city} onChange={props.change('city')} type="text" className="form-control" id="city" />
                        <label htmlFor="city">City</label>
                    </div>
        
                    <button className="w-50 btn btn-lg btn-dark" type="submit">Next Step</button>
                    <p className="mt-5 mb-3 text-muted">&copy; 2017–2021</p>
                </form>
            </div>
        </div>
    )
}
