import React from 'react'

export default function Userdetails(props) {
    return (
        <div className="bb text-center">
            <div className="form-signin bg-light">
                <form onSubmit={props.nextStep}>
                    <img className="mb-4" src="https://www.dropbox.com/s/zgbbayj1iqd9fjf/CF_Mark.jpg?raw=1" alt="" width="72"/>
                    <h1 className="h3 mb-3 fw-normal">Please Fill in The information below</h1>
        
                    <div className="form-floating">
                        <input required value={props.values.edu} onChange={props.change('edu')} type="text" className="form-control" id="floatingfName" />
                        <label htmlFor="floatingfName">Education</label>
                    </div>
                    <div className="form-floating">
                        <input required value={props.values.skill} onChange={props.change('skill')} type="text" className="form-control" id="floatinglName" />
                        <label htmlFor="floatinglName">English Skill</label>
                    </div>
                    <div className="form-floating">
                        <input required value={props.values.work} type="email" onChange={props.change('work')} className="form-control" id="floatingInput"/>
                        <label htmlFor="floatingInput">Work Experience</label>
                    </div>
                    <div className="form-floating">
                        <input required value={props.values.hoppies} type="text" onChange={props.change('hoppies')} className="form-control" id="country" />
                        <label htmlFor="country">Hoppies</label>
                    </div>        
                    <button onClick={props.prevStep} className="w-30 btn btn-lg btn-secondary m-2" type="submit">Previous Step</button>
                    <button className="w-30 btn btn-lg btn-dark m-2" type="submit">Next Step</button>
                    <p className="mt-5 mb-3 text-muted">&copy; 2017–2021</p>
                </form>
            </div>
        </div>
    )
}
