import React from 'react'

export default function Confirm(props) {
    return (
        <div className="bb text-center">
            <div className="form-signin bg-light">
                <form onSubmit={props.nextStep}>
                    <img className="mb-4" src="https://www.dropbox.com/s/zgbbayj1iqd9fjf/CF_Mark.jpg?raw=1" alt="" width="72"/>
                    <h1 className="h3 mb-3 fw-normal">Please make sure the informations are correct and confirm</h1>
        
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between">
                            <span>First Name :</span>
                            <span>{props.values.firstName}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between">
                            <span>Last Name :</span>
                            <span>{props.values.lastName}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between">
                            <span>E-mail : </span>
                            <span>{props.values.email}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between" >
                            <span>Country :</span>
                            <span>{props.values.country}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between"  >
                            <span>City :</span>
                            <span>{props.values.city}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between">
                            <span>Education :</span>
                            <span>{props.values.edu}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between">
                            <span>Skills :</span>
                            <span>{props.values.skill}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between">
                            <span>Workd Experience :</span>
                            <span>{props.values.work}</span>
                        </div>
                    </div>
                    <div className="form-floating">
                        <div className="form-control d-flex justify-content-between">
                            <span>Hoppies :</span>
                            <span>{props.values.hoppies}</span>
                        </div>
                    </div>        

                    <button onClick={props.prevStep} className="w-30 btn btn-lg btn-secondary m-2" type="submit">Back</button>
                    <button className="w-30 btn btn-lg btn-primary m-2" type="submit">Confirm</button>
                    <p className="mt-5 mb-3 text-muted">&copy; 2017–2021</p>
                </form>
            </div>
        </div>
    )
}
