import React from 'react'

export default function Success(props) {
    return (
        <div className="bb text-center">
            <div className="form-signin bg-light">
                <form onSubmit={props.nextStep}>
                    <img className="mb-4" src="https://www.dropbox.com/s/zgbbayj1iqd9fjf/CF_Mark.jpg?raw=1" alt="" width="72"/>
                    <h1 className="h3 mb-3 fw-normal text-primary">Thank you For Submission Please keep tuned for E-mail From us</h1>
                    <p className="mt-5 mb-3 text-muted">&copy; 2017–2021</p>
                </form>
            </div>
        </div>
    )
}
