import React, { Component } from 'react'
import './app.css'
import Personaldetails from './personaldetails';
import Userdetails from './userdetails';
import Confirm from './confirm';
import Success from './success';
export default class App extends Component {
    state = {
        firstName: '',
        lastName: '',
        email: '',
        country: '',
        city:'',
        edu:'',
        skill:'',
        work:'',
        hoppies:'',
        step:1
        
    }
    nextStep = (e) => {
        e.preventDefault()
        const { step } = this.state;
        this.setState({step:step+1})
    }
    prevStep = (e) => {
        e.preventDefault();
        const { step } = this.state;
        this.setState({step:step-1})
    }
    handleChange = input=>e => {
        this.setState({[input]:e.target.value})
    }
    render() {
        const { step, firstName, lastName, city, country, email,edu,skill,work,hoppies } = this.state
        const values = { firstName, lastName, city, country, email,edu,skill,work,hoppies}
        switch (step) {
            case 1:
                return (
            <Personaldetails
                nextStep={this.nextStep}
                change={this.handleChange}
                values={values}
            />
                )
            case 2:
                return (
            <Userdetails
                nextStep={this.nextStep}
                prevStep={this.prevStep}
                change={this.handleChange}
                values={values}
                    />
                )
            case 3:
                return (
                    <Confirm
                nextStep={this.nextStep}
                prevStep={this.prevStep}
                values={values}
                    />
                )
            case 4:
                return (
                    <Success/>
                )
            default:
                return (
            <Personaldetails
                nextStep={this.nextStep}
                change={this.handleChange}
                values={values}
            />
                )
        }
    }
}
